import { withStyles, createStyles, WithStyles, Typography, Link, Paper, Box, Tabs, Tab, Theme, AppBar } from "@material-ui/core";
import IssuesList from "./IssuesList";
import React from "react";
import { Page, Task, Result, IssueType } from "./models";

const styles = (theme: Theme) => createStyles({
    tabsRoot: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.paper,
    },
    tabIndicator: {
        backgroundColor: theme.palette.grey[500],
        minWidth: theme.spacing(20)
    },
    errorTabHeader: {
        color: theme.palette.error.main
    },
    warningTabHeader: {
        color: theme.palette.warning.main
    },
    noticeTabHeader: {
        color: theme.palette.info.main
    },
    tabPanelContainer: {
        padding: theme.spacing(2)
    }
});

export interface TaskPageResultProps extends WithStyles<typeof styles> {
    page: Page;
    task: Task;
    results: Result[];
};

interface TabPanelProps {
    id: string;
    currentId: string;
}

const TabPanel = (props: React.PropsWithChildren<TabPanelProps>) => {
    const { children, currentId, id, ...other } = props;

    return (
        <Box
            component="div"
            role="tabpanel"
            hidden={currentId !== id}
            id={`full-width-tabpanel-${id}`}
            aria-labelledby={`full-width-tab-${id}`}
            {...other}
        >
            {children}
        </Box>
    );
};

const a11yProps = (tabId: string) => {
    return {
        id: `full-width-tab-${tabId}`,
        'aria-controls': `full-width-tabpanel-${tabId}`,
    };
};

const TaskPageResult = (props: TaskPageResultProps) => {
    const { page, task, results, classes } = props;

    const issues = results.map((r) => r.issue);
    const errorIssues = issues.filter((issue) => issue.type == IssueType.Error);
    const warningIssues = issues.filter((issue) => issue.type == IssueType.Warning);
    const noticeIssues = issues.filter((issue) => issue.type == IssueType.Notice);

    const completedDate = new Date(task.completedDate * 1000);

    const [currentTabId, setCurrentTabId] = React.useState<string>(IssueType.Error);

    const handleTabChange = (event: React.ChangeEvent<{}>, newTabId: string) => {
        setCurrentTabId(newTabId);
    };

    return (
        <div>
            <Typography variant="h5" component="h2">
                {page.title}
            </Typography>
            <Typography variant="subtitle1">
                <Link href={page.url} target="_blank">
                    {page.url}
                </Link>
                <br />
                <em>Last scanned at {completedDate.toLocaleDateString()} {completedDate.toLocaleTimeString()}</em>
            </Typography>

            <div className={classes.tabsRoot}>
                <Paper>
                    <Tabs classes={{ indicator: classes.tabIndicator }} value={currentTabId} onChange={handleTabChange} aria-label="A11y results tabs by types">
                        <Tab className={classes.errorTabHeader} label={`Errors (${errorIssues.length})`} value={IssueType.Error} {...a11yProps(IssueType.Error)} />
                        <Tab className={classes.warningTabHeader} label={`Warnings (${warningIssues.length})`} value={IssueType.Warning} {...a11yProps(IssueType.Warning)} />
                        <Tab className={classes.noticeTabHeader} label={`Notices (${noticeIssues.length})`} value={IssueType.Notice} {...a11yProps(IssueType.Notice)} />
                    </Tabs>
                </Paper>
                <Box className={classes.tabPanelContainer}>
                    <TabPanel currentId={currentTabId} id={IssueType.Error}>
                        <IssuesList issues={errorIssues} issueType={IssueType.Error} />
                    </TabPanel>
                    <TabPanel currentId={currentTabId} id={IssueType.Warning}>
                        <IssuesList issues={warningIssues} issueType={IssueType.Warning} />
                    </TabPanel>
                    <TabPanel currentId={currentTabId} id={IssueType.Notice}>
                        <IssuesList issues={noticeIssues} issueType={IssueType.Notice} />
                    </TabPanel>
                </Box>
            </div>
        </div>
    )
};

export default withStyles(styles)(TaskPageResult);